.appointment-item-card {
  border: 1px solid #b5b7c4;
  border-radius: 10px;
  margin: 15px;
  display: flex;
  justify-content: space-between;
}

.appointment-text {
  font-size: 15px;
  margin: 10px;
  font-weight: 1000;
}

.date-text {
  font-size: 12px;
  color: #b5b7c4;
  margin-left: 10px;
  margin-bottom: 15px;
  font-weight: 500;
}

.star-success-button-container {
  display: flex;
}

.star-logo {
  height: 20px;
  width: 20px;
  margin: 10px;
}

.star-button {
  border-width: 0;
  background-color: transparent;
  height: fit-content;
}

.done-button {
  height: 25px;
  width: 80px;
  font-size: 15px;
  border: 1px solid #97f0b6;
  border-radius: 20px;
  background-color: transparent;
  color: #97f0b6;
  font-weight: 500;
  margin: 8px;
}

.display-none {
  display: none;
}

.delete-button-container {
  display: flex;
  justify-content: flex-end;
  width: 90%;
  margin: 5px;
}

.delete-button {
  border-width: 0;
  background-color: transparent;
  height: fit-content;
  cursor: pointer;
}

.delete-logo {
  height: 20px;
  width: 20px;
}

@media (min-width: 768px) {
  .appointment-item-card {
    justify-content: flex-start;
    width: fit-content;
  }
  .appointment-text {
    font-size: 20px;
    margin: 20px;
    width: 250px;
    word-wrap: break-word;
  }
  .date-text {
    font-size: 17px;
    margin: 0 0 20px 20px;
  }
  .star-logo {
    height: 30px;
    width: 30px;
    margin: 20px;
  }
  .done-button {
    margin-top: 22px;
  }
  .delete-logo {
    height: 30px;
    width: 30px;
  }
}
