* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto";
}

.bg-container {
  background-image: linear-gradient(to top, #9796f0, #fbc7d4);
  min-height: 100vh;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-y: scoll;
}

.appointment-card {
  width: 100%;
  margin: 15px;
  border-radius: 10px;
  background-color: #ffffff;
}

.appointments-heading {
  font-size: 20px;
  margin: 20px 20px 15px 15px;
}

.label-input {
  display: block;
  font-size: 12px;
  color: #b5b7c4;
  font-weight: 500;
  margin: 0 0 5px 15px;
}

.input-text {
  display: block;
  height: 35px;
  width: 230px;
  border-radius: 5px;
  border: 1px solid #171f46;
  outline: none;
  padding: 10px;
  font-size: 15px;
  margin: 0 0 15px 15px;
}

::placeholder {
  color: #b5b7c4;
}

.button-add-today-container {
  display: flex;
  justify-content: flex-start;
  width: fit-content;
  margin: 0 0 15px 15px;
}

.add-appointment-button {
  display: inline;
  height: 35px;
  width: 60px;
  background-color: #8b5cf6;
  border-width: 0;
  border-radius: 10px;
  color: #ffffff;
  font-size: 15px;
  cursor: pointer;
  outline: none;
  margin-right: 20px;
}

.separation-line {
  border: 1.5px solid #b5b7c4;
  margin: 15px;
}

.appointment-list-heading-button-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 15px;
}

.appointments-list-heading {
  font-size: 20px;
}

.appointment-button-container {
  display: flex;
}

.starred-button {
  height: 25px;
  width: 90px;
  font-size: 15px;
  border: 1px solid #9897f0;
  border-radius: 20px;
  background-color: transparent;
  color: #9897f0;
  font-weight: 500;
  margin: 5px;
}

.active-starred-button {
  height: 25px;
  width: 90px;
  font-size: 15px;
  border: 1px solid #9897f0;
  border-radius: 20px;
  background-color: #9897f0;
  color: #ffffff;
  font-weight: 500;
  margin: 5px;
}

.completed-button {
  height: 25px;
  width: 90px;
  font-size: 15px;
  border: 1px solid #97f0b6;
  border-radius: 20px;
  background-color: transparent;
  color: #97f0b6;
  font-weight: 500;
  margin: 5px;
}

.active-completed-button {
  height: 25px;
  width: 90px;
  font-size: 15px;
  border: 1px solid #97f0b6;
  border-radius: 20px;
  background-color: #97f0b6;
  color: #ffffff;
  font-weight: 500;
  margin: 5px;
}

.appointments-list-container {
  list-style-type: none;
}

.appointments-image {
  display: none;
}

.today-date {
  color: purple;
}

@media (min-width: 768px) {
  .appointment-card {
    width: 90%;
    border-radius: 15px;
  }
  .appointment-form {
    display: flex;
  }
  .appointments-heading {
    font-size: 35px;
    margin: 50px 40px 30px 40px;
  }
  .label-input {
    font-size: 20px;
    margin: 0 40px 15px 40px;
  }
  .input-text {
    height: 45px;
    width: 300px;
    font-size: 20px;
    margin: 0 40px 30px 40px;
    padding: 20px;
  }
  .button-add-today-container {
    margin: 0 40px 60px 40px;
  }
  .add-appointment-button {
    height: 50px;
    width: 90px;
    font-size: 20px;
  }
  .separation-line {
    margin: 0 40px 0 40px;
  }
  .appoinments-display-section {
    margin: 15px;
  }
  .appointment-list-heading-button-box {
    margin: 40px;
  }
  .appointments-list-heading {
    font-size: 35px;
  }
  .starred-button {
    height: 45px;
    width: 130px;
    font-size: 20px;
    margin-right: 25px;
  }
  .active-starred-button {
    height: 45px;
    width: 130px;
    font-size: 20px;
    margin-right: 25px;
  }
  .completed-button {
    height: 45px;
    width: 130px;
    font-size: 20px;
    margin-right: 25px;
  }
  .active-completed-button {
    height: 45px;
    width: 130px;
    font-size: 20px;
    margin-right: 25px;
  }
  .appointments-image {
    display: inline;
    height: 250px;
    width: 270px;
    margin: 110px 40px 0 0;
  }
  .appointments-list-container {
    display: flex;
    flex-wrap: wrap;
    margin: 40px;
  }
}

@media (min-width: 992px) {
  .appointment-card {
    width: 1300px;
    border-radius: 15px;
    margin: 70px 0 70px 0;
  }
  .appointment-form {
    display: flex;
  }
  .appointments-heading {
    font-size: 40px;
    margin: 50px 40px 50px 60px;
  }
  .label-input {
    font-size: 20px;
    margin: 0 40px 15px 60px;
  }
  .input-text {
    height: 60px;
    width: 350px;
    font-size: 20px;
    margin: 0 40px 30px 60px;
  }
  .appointments-image {
    display: inline;
    height: 410px;
    width: 450px;
    margin: 80px 40px 0 0;
  }
  .button-add-today-container {
    margin: 0 40px 40px 60px;
  }
  .add-appointment-button {
    height: 50px;
    width: 90px;
    font-size: 20px;
  }
  .separation-line {
    margin: 0 60px 0 60px;
  }
  .appointment-list-heading-button-box {
    margin: 40px;
    margin-left: 60px;
  }
  .appointments-list-heading {
    font-size: 35px;
  }
  .starred-button {
    height: 45px;
    width: 130px;
    font-size: 20px;
  }
  .active-starred-button {
    height: 45px;
    width: 130px;
    font-size: 20px;
  }
  .completed-button {
    height: 45px;
    width: 130px;
    font-size: 20px;
  }
  .active-completed-button {
    height: 45px;
    width: 130px;
    font-size: 20px;
  }
}

@media (min-width: 1200px) {
  .appointment-form {
    justify-content: space-between;
  }
  .appointments-image {
    display: inline;
    height: 380px;
    width: 500px;
    margin: 130px 100px 0 50px;
  }
}
